.row__poster{
    object-fit: contain;
    width: 100%;
    max-height: 100px;
    margin-right: 10px;
    transition: transform 450ms;
    border-radius: 3px;

} 

.row__posters::-webkit-scrollbar{
    display: none;
}

.row__posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
    

}

.row__poster:hover{
    /* width: 120%;
    max-height: 120px; */
    transform: scale(1.08);
    border-radius: 5px;
}

.row__posterLarge{
    max-height: 250px;

}

.row__posterLarge:hover{
   transform: scale(1.10);

}